// This file will contain set of functions needed to communicate with the native part of the application

// Postmessage can be used to send info/signals uphill (into the mobile app)
const postMessage = (method, message) => {
  if (!window.webkit || window.webkit.messageHandlers) {
    window.console.error(
      'Mediator works only inside WebView environment. Fallback to alert'
    );
    // eslint-disable-next-line no-alert
    window.alert(
      `mediator.${method} has been called with next message:
      \n${JSON.stringify(message)}`
    );
  } else if (
    !window.webkit.messageHandlers[method] ||
    !window.webkit.messageHandlers[method].postMessage
  ) {
    throw new Error(`There're no method ${method} on ViewController`);
  } else {
    window.webkit.messageHandlers[method].postMessage(message);
  }
};

export const navigateBack = () => {
  return postMessage('navigateBack');
};

export const openEmail = () => {
  return postMessage('openEmail');
};

export const navigateLinkedAccounts = () => {
  return postMessage('navigateLinkedAccounts');
};

