import React from 'react';
import { navigateBack, navigateLinkedAccounts } from 'utils/mediator';
import Page from 'components/Page';
import List, { ListItem } from 'components/List';
import AppVersion from 'components/AppVersion';
import WithFooter from 'components/WithFooter';

const CreatorAccount = ({ history }) => {
  const handle = localStorage.getItem('form-value-handle') || 'spotravnyy';
  const email =
    localStorage.getItem('form-value-email') || 'spotravnyy@lohika.com';

  return (
    <Page
      className="topLevelPage"
      title="Account Settings"
      onBackNavigation={() => navigateBack()}
    >
      <WithFooter footer={<AppVersion />}>
        <List>
          <ListItem
            title="Brand name"
            onClick={() => history.push('/creator/account/handle')}
          >
            {handle}
          </ListItem>

          <ListItem
            title="Email"
            onClick={() => history.push('/creator/account/email')}
          >
            {email}
          </ListItem>

          <ListItem onClick={() => history.push('/creator/account/password')}>
            Update Password
          </ListItem>

          <ListItem onClick={() => history.push('/creator/account/categories')}>
            Edit categories
          </ListItem>

          <ListItem onClick={() => navigateLinkedAccounts()}>
            Linked Accounts
          </ListItem>
        </List>
      </WithFooter>
    </Page>
  );
};

export default CreatorAccount;
